<template>
  <div class="page bg">
    <header class="ctn">
      <back-link></back-link>
    </header>
    <main class="ctn">
      <div class="fs-xl bolder mt-lg mb-md">{{ signinTitle }}</div>
      <div class="fs-sm c-hint mb-xl">{{ signinHint }}</div>
      <r-form class="row" action="/sessions" @ok="$emit('ok')">
        <input type="hidden" name="category" :value="category">
        <div class="col-md-6">
          <input ref="mobile" type="tel" name="mobile" v-model="mobile" placeholder="" class="input" maxlength="11" required v-autofocus>
          <mobile-captcha v-if="byMobileCaptcha || byMobileCaptchaPassword" :url="`/captchas?type=${captchaType}`" :mobile="mobile" @fail="$refs.mobile.focus()"></mobile-captcha>
          <input v-if="byMobile" type="password" name="password" placeholder="" class="input" required>
          <input v-if="byMobileCaptchaPassword" type="password" name="password" placeholder=" (8)" class="input" required>
          <div class="br-md"></div>
          <input type="submit" value="" class="btn primary block my-lg">
        </div>
      </r-form>
      <div class="li">
        <main>
          <button @click="switchCategory()" class="fs-sm">{{ switchHint }}</button>
        </main>
        <button @click="switchCategory('by_mobile_captcha_password')" class="fs-sm" v-if="byMobile"></button>
      </div>
      <div class="br-xl my-xl"></div>
      <p class="tac fs-sm c-hint">
        <span></span>
        <br>
        <router-link :to="{ name: 'terms_of_use', query: { backable: true } }" class="c-major"></router-link>
        <span></span>
        <router-link :to="{ name: 'privacy_policy', query: { backable: true } }" class="c-major"></router-link>
      </p>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobile: null,
      category: this.$route.query['category'] || 'by_mobile_captcha'
    }
  },
  computed: {
    byMobile() {
      return this.category === 'by_mobile'
    },
    byMobileCaptcha() {
      return this.category === 'by_mobile_captcha'
    },
    byMobileCaptchaPassword() {
      return this.category === 'by_mobile_captcha_password'
    },
    signinTitle() {
      return this.byMobileCaptchaPassword ? '' : ''
    },
    signinHint() {
      return this.byMobileCaptchaPassword ? '' : this.byMobile ? '' : ''
    },
    switchHint() {
      return this.byMobile ? '' : ''
    },
    captchaType() {
      return this.byMobileCaptchaPassword ? 'password_forgetting_mobile' : 'signin_mobile'
    }
  },
  methods: {
    switchCategory(newCategory) {
      this.category = newCategory || (this.byMobile ? 'by_mobile_captcha' : 'by_mobile')
      this.$refs.mobile.focus()
    }
  }
}
</script>
